import React, { useState, Suspense } from 'react'

import Error from '../../Components/Error/Error'
import Spinner from '../../Components/UI/Spinner/Spinner'

import links from '../../Assets/Links/Links'
import classes from './IgPosts.module.css'

const IgPost = React.lazy(() => import('./IgPost/IgPost'))

const IgPosts = () => {
  const [err, setErr] = useState(false)

  const postLoadFailure = () => {
    setErr({
      heading: "Error Occured",
      message: "Some Error Occured, maybe due to slow internet. Please reload."
    })
  }

  const onRetry = () => {
    window.location.reload()
  }

  return (
    <div className={`${classes.wrapper} section`}>
      <h2 className={classes.heading}>My Instagram Posts :</h2>

      <p className={classes.description}>
        I upload educational posts on <a rel="noreferrer noopener" className={`link`} target="_blank" href={links.ig}>Instagram</a> do follow me there.<br />
        Here are few posts...
      </p>

      <div className={classes.container}>
        {!err ?
        <Suspense fallback={<Spinner />}>
          <IgPost onFail={postLoadFailure} />
        </Suspense>
        : <Error heading={err.heading} retryBtn onClickRetry={onRetry} message={err.message} />}
      </div>
    </div>
  )
}

export default IgPosts
