import React from 'react';

import classes from './Spinner.module.css';

const Spinner = props => {
    return (
		<div
			className={`${classes.circleLoader} ${props.className ? props.className : ''} 
			${props.status === 'completed' ? classes.loadComplete : props.status === "error" ? classes.error : ``}`}>
				<div className={`${props.status === "completed" ? `${classes.checkmark} ${classes.draw}` : 
				props.status === "error" ? `${classes.wrong} ${classes.draw}` : ''}`}></div>
        </div>
    );
}

export default Spinner;