import React from 'react'

import classes from './Footer.module.css'
import links from '../../Assets/Links/Links'

const Footer = () => {
	const contactLinks = [
		{ url: links.ig, name: 'Instagram' },
		{ url: links.fiverr, name: 'Fiverr' },
		{ url: links.email, name: 'E-mail' },
		{ url: links.github, name: 'Github' },
		{ url: links.fb, name: 'Facebook' },
	]
	return (
		<footer className={`${classes.container} section`}>
			<div className={classes.contactContainer}>
				<span className={classes.contactHeading}>Contact :</span>
				<div className={classes.linkContainer}>
					{
						contactLinks.map((link, i) => {
							return (
								<a
									target="_blank"
									rel="noreferrer noopener"
									className={`link ${classes.link}`}
									href={link.url}>
										{link.name}
								</a>
							)
						})
					}
				</div>
			</div>
			<div className={classes.rights}> {new Date().getFullYear()} Shashank. All rights reserved.</div>
		</footer>
	)
}

export default Footer
