import React, { useState, useEffect } from 'react'

import Logo from '../../Components/Logo/Logo'
import NavLinks from './NavLinks/NavLinks'
import Burger from '../../Components/UI/Burger/Burger'
import classes from './Navigation.module.css'

const Navigation = () => {
	const [smallSize, setSmallSize] = useState(false)
	const [openNav, setOpenNav] = useState(false)

	const windowResizeHandler = () => {
		if (window.innerWidth <= 1100) {
			setSmallSize(true)
		} else {
			setSmallSize(false)
		}
	}

	const burgerClicked = () => {
		setOpenNav(oldVal => !oldVal)
	}

	const onLinkClick = () => {
		setOpenNav(() => false)
	}

	useEffect(() => {
		window.addEventListener("resize", windowResizeHandler)
		window.addEventListener('hashchange', onLinkClick)

		windowResizeHandler()
		return () => {
			window.removeEventListener("resize", windowResizeHandler)
		}
	}, [])

	return (
		<>
		<div id="home"></div>
		<div className={`${classes.container} section`}>
			<Logo small={smallSize} />
			<NavLinks open={openNav} />
			<Burger className={classes.hideBurger} onClick={burgerClicked} />
		</div>
		</>
	)
}

export default Navigation
