import React, { useState, useEffect } from 'react'

import classes from './NavLinks.module.css'
import ul from '../../../Components/UI/Underline/Underline.module.css'

const NavLinks = props => {
	const [navLinks, setNavLinks] = useState([
		{
			name: "Home",
			url: "#home",
			active: window.location.hash === "#home"
		},
		{
			name: "Projects",
			url: "#projects",
			active: window.location.hash === "#projects"
		},
		{
			name: "About Me",
			url: "#aboutme",
			active: window.location.hash === "#aboutme"
		},
		{
			name: "CONTACT",
			url: "#contact",
			highlight: true,
			active: window.location.hash === "#contact"
		}
	])
	
	const windowScrollHandler = () => {
		const elId = ['home', 'projects', 'aboutme', 'contact']
		const el = []
		elId.forEach(id => {
			el.push(document.getElementById(id).getBoundingClientRect())
		})

		if (window.scrollY === 0 || window.scrollY < Math.ceil(el[1].y + window.scrollY + 80 - window.innerHeight)) {
			setNavLinks(oldLinks => {
				const newLinks = [...oldLinks]
				newLinks.forEach((link, i) => {
					link.active = false
				})
				newLinks[0].active = true
				return newLinks
			})
		} else if ((Math.floor(el[1].y) <= 0 && Math.floor(el[1].y) > -15) || (Math.floor(el[2].y + 80 + 40) >= window.innerHeight)) {
			setNavLinks(oldLinks => {
				const newLinks = [...oldLinks]
				newLinks.forEach((link, i) => {
					link.active = false
				})
				newLinks[1].active = true
				return newLinks
			})													// nav height + top pos of #contact div
		} else if (Math.floor(el[2].y) <= 0 && Math.floor(el[3].y + 80 + 20) >= window.innerHeight) {
			setNavLinks(oldLinks => {
				const newLinks = [...oldLinks]
				newLinks.forEach((link, i) => {
					link.active = false
				})
				newLinks[2].active = true
				return newLinks
			})
		} else if (Math.floor(el[3].y) <= 0) {
			setNavLinks(oldLinks => {
				const newLinks = [...oldLinks]
				newLinks.forEach((link, i) => {
					link.active = false
				})
				newLinks[3].active = true
				return newLinks
			})
		}
	}

	useEffect(() => {
		window.addEventListener('scroll', windowScrollHandler)
		windowScrollHandler()
		return () => {
			window.removeEventListener('scroll', windowScrollHandler)
		}
	}, [])

	return (
		<nav className={`${classes.container} ${props.open ? classes.navOpen : ''}`}>
			<ul className={classes.linksContainer}>
				{
					navLinks.map((link, i) => {
						return (
							<li key={i} className={classes.linkContainer}>
								<a
									href={link.url}
									className={`${classes.links} ${ul.underline} ${link.active ? classes.active : ''} ${link.highlight ? classes.highlight : ''}`}>
										{link.name}
								</a>
							</li>
						)
					})
				}
			</ul>
		</nav>
	)
}

export default NavLinks
