import React, { useState, useEffect } from 'react'

import classes from './Intro.module.css'
import ul from '../../Components/UI/Underline/Underline.module.css'

const Intro = () => {
	const [openIntro, setOpenIntro] = useState(false)
	const [initialWelcTxt, setInitialWelcTxt] = useState(true)
	const [introTxt, setIntroTxt] = useState("")
	const [openLine, setOpenLine] = useState(false)
	const [cursor, setCursor] = useState(false)
	
	useEffect(() => {
		const fullIntroTxt = `I BUILD WEBSITES FOR YOU`

		const openIntroHandler = () => {
			setOpenIntro(true)
		}

		const smallWelcTxt = () => {
			setInitialWelcTxt(false)
		}

		const addCursor = () => {
			setCursor(true)
			blinkCursor()
		}

		const blinkCursor = () => {
			setInterval(() => {
				setCursor(oldVal => !oldVal)
			}, 500)
		}

		const writeIntroTxt = () => {
			const id = setInterval(() => {
				setIntroTxt(oldTxt => {
					if (oldTxt.length === fullIntroTxt.length) {
						clearInterval(id)

						return fullIntroTxt
					} else {
						return oldTxt + fullIntroTxt[oldTxt.length]
					}
				})
			}, 200)
		}

		const openLineHandler = () => {
			setTimeout(() => setOpenLine(true), 100)
		}

		setTimeout(openIntroHandler, 700)
		setTimeout(smallWelcTxt, 2300)
		setTimeout(addCursor, 3500)
		setTimeout(writeIntroTxt, 3500)
		setTimeout(openLineHandler, 3500 + (fullIntroTxt.length * 200) + 400)
	}, [])

	return (
		<div className={classes.wrapper}>
			<div className={`${classes.container} ${openIntro ? classes.introOpen : ''} section`}>
				<div className={classes.txtContainer}>
					<h2 className={`${initialWelcTxt ? classes.initialWelcTxt : ''} ${classes.welcTxt} ${classes.txt}`}>WELCOME TO THE CODING THERAPY</h2>
					<h1 className={`${classes.introTxt} ${classes.txt} ${cursor ? classes.cursor : ''}`}>{introTxt}</h1>
					<div className={`${ul.underline} ${openLine ? classes.line1 : ''}`}></div>
					<div className={`${ul.underline} ${openLine ? classes.line2 : ''}`}></div>
				</div>
			</div>
		</div>
	)
}

export default Intro
