import React, { useEffect, useState } from 'react'

import Parse from 'parse'

import Spinner from '../UI/Spinner/Spinner'
import classes from './Email.module.css'
import Tooltip from '../UI/Tooltip/Tooltip'

const Email = () => {
	const [loading, setLoading] = useState(false)
	const [err, setErr] = useState(false)
	const [success, setSuccess] = useState(false)
	const [sendReq, setSendReq] = useState(false)
	const [email, setEmail] = useState('')

	const emailSubmitHandler = () => {
		if (!sendReq) {
			setSendReq(true)
			setLoading(true)
			setErr(false)
		}
	}

	const emailInputHandler = e => {
		setEmail(e.target.value.toLocaleLowerCase())
		setLoading(false)
		setSuccess(false)
		setErr(false)
	}

	useEffect(() => {
		if (sendReq) {
			Parse.Cloud.run('saveEmailWithValidation', { email: email }).then(res => {
				setLoading(false)
				setSendReq(false)
				if (res.success) {
					setSuccess({success: true, message: res.message})
					setEmail("")
				} else if (res.invalidEmail) {
					setErr({error: true, message: res.message})
				} else if (res.emailExists) {
					setErr({error: true, message: res.message})
				} else if (res.error) {
					setErr({error: true, message: res.message})
				}
			}).catch(err => {
				setSendReq(false)
				setErr({...err})
			})
		}

	}, [sendReq, email])

	return (
		<div className={`${classes.container} section`}>
			<h2 className={classes.subscribeTxt}>SUBSCRIBE FOR EMAIL UPDATES</h2>
			<div className={classes.inputContainer}>
				<input
					className={`inp ${classes.emailInput}`}
					value={email}
					onChange={e => {emailInputHandler(e)}}
					type="email"
					placeholder="Email" /><br />
					<div className={classes.messageContainer}>
						{loading || err || success ? <Spinner className={classes.spinner} status={success ? "completed" : err ? "error" : ''} /> : null}
						{err || success ? <Tooltip className={classes.errMessage} text={success ? success.message : err.message} /> : null}
					</div>
				<button onClick={emailSubmitHandler} className={`btn ${classes.submitBtn}`}>Submit</button>
			</div>
		</div>
	)
}

export default Email
