import React from 'react'

import logoSrc from '../../Assets/Images/Logo/theCodingTherapy[tbwt].png'
import classes from './Logo.module.css'

const Logo = props => {
	return (
		<a href="/" className={classes.logoContainer}>
			<img
				className={`${classes.logo} ${props.small ? classes.small : classes.medium}`}
				src={logoSrc}
				alt="theCodingTherapy" />
		</a>
	)
}

export default Logo
